import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { Transactions } from 'src/app/Model/Transactions';
import { TransactionService } from 'src/app/services/transaction/transaction.service';

@Component({
  selector: 'app-company-outstandings',
  templateUrl: './company-outstandings.component.html',
  styleUrls: ['./company-outstandings.component.css']
})
export class CompanyOutstandingsComponent implements OnInit {

  constructor(private _transactionService:TransactionService) { }
  Outstandings:any;
  isVisible:boolean;
  companyObserverSubject:Subject<Transactions> = new Subject();
  ngOnInit(): void {
    this._transactionService.transactionForCompanyObserver$.subscribe(res=>{
      this.Outstandings=res;
    });
    
  }

}
