import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import { Transactions } from 'src/app/Model/Transactions';
import { TransactionService } from 'src/app/services/transaction/transaction.service';

class FilterObject {
  constructor(private data: any) { }


  agent: string = this.data.agent;

}

@Component({
  selector: 'app-agent-outstadings',
  templateUrl: './agent-outstadings.component.html',
  styleUrls: ['./agent-outstadings.component.css']
})
export class AgentOutstadingsComponent implements OnInit {

  constructor(private _transacionService:TransactionService) { }

  search: string;
  AgentOutStandings:any;
  listDataCopy: string;
  isVisible:boolean;
  companyObserverSubject:Subject<Transactions> = new Subject();
  ngOnInit(): void {
    this._transacionService.transactionForAgentObserver$.subscribe(res=>{
      this.AgentOutStandings=res;
      this.listDataCopy = JSON.stringify(this.AgentOutStandings);
    });
    
  }
  filter() {
    this.AgentOutStandings = JSON.parse(this.listDataCopy);
    if (this.search !== "") {
      this.AgentOutStandings = this.AgentOutStandings.filter(item => {
        let data = new FilterObject(item);
        return Object.keys(data).some(
          k =>
            data[k] != null &&
            data[k]
              .toString()
              .toLowerCase()
              .includes(this.search.toLowerCase())
        )
      });
    }
  }

}
